import React from 'react'

function Header() {
    return (
        <div className="header">
            <div className="container-fluid">
                <div className="row">
                    <div className="col">
                        <button className="btn act">Activity <img src="Assets/Polygon1.svg" alt="arrow" /></button>
                        <button className="btn act">Tool Guide <img src="Assets/Polygon1.svg" alt="arrow" /></button>
                    </div>
                    <div className="col">
                        <div className="right">
                            <div class="form-group has-search">
                                <img class="p-2 form-control-feedback" src="Assets/Ellipse1.svg" alt="search" />
                                <input type="text" class="form-control" placeholder="Search..." />
                            </div>
                            <div className="btngroup">
                                <button className="btn">
                                    <img src="Assets/Page1.svg" alt="bell" />
                                    <img className="mx-2" src="Assets/Ellipse2.svg" alt="img" />
                                    <img src="Assets/Polygon3.svg" alt="arrow" />
                                </button>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    )
}

export default Header
