import React from 'react';
import Header from './Header';
import Graph from './Graph';

function Main() {
    return (
        <>
            <Header />
            <div className="container my-4">
                <div className="row">
                    <div className="col bitcoin">
                        <img className="mx-3" src="Assets/bitcoin.svg" alt="Bitcoin" />
                        <span>Real-time bitcoin graph</span>
                    </div>
                    <div className="col">
                        <div className="comodity">
                            <div className="dropdown me-5">
                                <button className="btn dropdown-toggle px-5" type="button" id="dropdownMenuButton" data-bs-toggle="dropdown" aria-expanded="false">
                                    Commodity 1
                            </button>
                                <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                </ul>
                            </div>
                            <div className="dropdown">
                                <button className="btn dropdown-toggle px-5" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                                    Commodity 2
                            </button>
                                <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                    <li><a className="dropdown-item" href="#">Option 1</a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Graph />
        </>
    )
}

export default Main
