import React from 'react';
import { Line } from 'react-chartjs-2';
const data = {
    labels: ['x1', 'x2', 'x3', 'x4', 'x5'],
    datasets: [{
        data: [12, 80, -30, 10, 55],
        label: "Commodity1",
        borderColor: "#5843be",
        fill: false
    }, {
        data: [-30, 10, 45, -50, 20],
        label: "Commodity2",
        borderColor: "#c30f70",
        fill: false
    }]
};
const options = {
    title: {
        display: true,
        text: 'World population per region (in millions)'
    }
};
function Graph() {
    return (
        <div className="container-fluid graph">
            <Line data={data} />
        </div>
    )
}

export default Graph
